import styled, { css } from "styled-components";

const getGridGutter = ({ theme, fullWidth }) =>
  fullWidth ? 0 : theme.gridGutter;

const getPadding = ({ theme, padding }) => (padding ? theme.gridGutter : 0);

const getMaxWidthOffset = props =>
  (getGridGutter(props) + getPadding(props)) * 2;

const getBreakpoint = breakpoint => css`
  @media screen and (min-width: ${breakpoint}px) {
    max-width: calc(${breakpoint}px - ${getMaxWidthOffset}px);
  }
`;

export default styled.div`
  width: 100%;
  margin: 0 auto;
  padding: ${getPadding}px;
  max-width: calc(100% - ${getMaxWidthOffset}px)
    ${({ fullWidth }) => fullWidth && "!important"};

  ${({ theme }) => theme.breakpoints.map(getBreakpoint)};
`;
